/* CSS perso */

body{
  font-family:Gill Sans, sans-serif;
}

.back{
  background-image:url(/src/img/background.jpg);
  background-attachment:fixed;
  padding: 1%;
}

.page{
  border-radius: 10px;
  background-color: white;
  opacity: 0.97;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.profil_img{
  margin-left: auto;
  margin-right: auto;
  display: block;
  width:200px;
  height: 200px;
  object-fit: cover;
}

.profil_pad{
  padding-top:2.5%;
}

.profil_center{
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.profil_contour_img{
  margin-left: auto;
  margin-right: auto;
  display: block;
  overflow: hidden;
  width: 200px;
  border: solid 5px black;
  border-radius: 100%;
}

.profil_nom{
  margin-left: auto;
  margin-right: auto;
  display: block;
  color:black;
  font-weight: 1000;
  font-family: Impact, fantasy, sans-serif;
  font-size: 2em;
  text-align:center;
}

.infos{
  padding-left: 2.5%;
  padding-right: 2.5%;
}

.infos_a:hover{
  color:black;
}

.infos_espace{
  padding-top: 10%;
}

.infos_description {
  border: blue ridge;
  border-radius: 10px;
  padding: 2%;
  font-style: italic;
  font-family: arial, sans-serif;
}

.infos_description_texte {
  color: black;
  font-size: 2em;
  text-shadow: 1px 1px gray;
}

.infos_line{
  width:100%;
  flex-wrap: wrap;
  grid-column: 1 / 2;
  grid-row: 1;
}

.infos_text{
  text-align: center;
  border:  solid;
  font-weight:1000;
  font-size: 2em;
  padding-top: 20px;
  padding-bottom: 20px;
  border-radius: 10px;
}

.infos_mail{
  background-color: red;
  color:white;
  margin-left:2%;
}

.infos_tel{
  background-color:#439B52;
  color: white;
  margin-right:2%;
}

.infos_linkedin{
  background-color: #0072b1;
  color:white;
  margin-left:2%;
}

.infos_cv{
  background-color: gray;
  color:white;
  margin-right:2%;
  border: solid black 2px;
}

.infos_grille{
  display: grid;
  grid-template-columns: 50% 50%;
}

a:link{
  color:white;
  text-decoration: none;
}

a:visited {
  color:white;
  text-decoration: none;
}

a:hover {
  color:white;
  text-decoration: none;
}

a:active {
  color:white;
  text-decoration: none;
}


.skill_espace{
  padding-top:10%;
}

.skill_titre{
  font-size: 3em;
  font-weight: bold;
  text-align: center;
}

.skill_centre{
  margin-left: auto;
  margin-right: auto;
  display:block;
}

.skill_centre_skill{
  margin-left: auto;
  margin-right: auto;
}

.skill_bordure{
  border:gray solid;
  border-radius: 10px;
}

.skill_width{
  max-width:100%;
}

.skill_border{
  border :solid gray;
  margin : 3%;
  border-radius: 10px;
}

.skill_border2{
  border :solid gray;
  margin-right: auto;
  margin-left: auto;
  display: block;
  border-radius: 10px;
  width: 15%;
}

.skill_center{
  margin-left: auto;
  margin-right: auto;
  display:block;
}

.skill_padding{
  padding-top: 2%;
  padding-bottom: 2%;
}

.skill_text{
  font-size: 1.5em;
}

.skill_li{
  font-size: 1.5em;
}

.skill_pad_text{
  padding-top:1%;
  padding-bottom:1%;
}

.skill_flex{
  display:flex;
}

.skill_logo{
  margin-left: auto;
  margin-right: auto;
  display: block;
  text-align: center;
}


.projet_flex{
  display:inline-block;
  width:20%;
  font-family: "Gill Sans", sans-serif;
  padding-left: 2.5%;
  padding-right: 2.5%;
}

.projet_flex button{
  font-size: 1.5em;
  padding: 10px;
  width:100%;
  color:white;
  font-weight: bold;
  background-color: #2444f8;
  border:black solid 2px;
  border-radius: 3px;
  height: 90px;
  border: solid black 3px;
  border-radius: 10px;
}

.projet_flex button:hover{
  background-color: white;
  color:#2444f8;
}

.projet_align{
  display:block;
  margin-left: auto;
  margin-right: auto;
}

.projet_center{
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.projet_espace{
  padding-top: 10%;
}

.projet_centrer{
  text-align: center;
}

.projet_titre2{
  font-size: 2.5em;
  text-align: center;
  padding: 20px;
}

.projet_titre{
  font-size: 2em;
  text-align: center;
}

.projet_github{
  color:black;
  text-align: center;
  font-size: 5em;
  border:black solid;
  border-radius: 5px;
  margin-right: auto;
  margin-left: auto;
  width:25%;
}

.projet_a{
  margin-right: auto;
  margin-left: auto;
  width:25%;
}

.projet_github a:link{
  color:black;
}

.projet_github a:visited{
  color:black;
}

.projet_github a:hover{
  color: black;
}

.projet_github a:hover{
  color: white;
  background-color: black;
}

.projet_github:hover{
  color: white;
  background-color: black;
}

.projet_button{
  font-size: 2em;
  margin-left: auto;
  margin-right: auto;
  display: block;
  width:50%;
}

.projet_button_modal{
  cursor: pointer;
}

.projet_modal{
  opacity:5;
}


.hobbie_space{
  padding-top: 10%;
}

.hobbies_align{
  display: inline-block;
  width: 50%;
}

.hobbie_titre{
  text-align: center;
  font-size: 2em;
}

.hobbie_contour{
  border:gray solid;
  border-radius: 10px;
  margin: 2%;
  padding: 2%;
}

.hobbie_img_center{
  margin-right: auto;
  margin-left: auto;
  display: block;
}

.langue_hobbie_texte{
  font-size: 40px;
  text-align: center;
}

.hobbie_texte{
  font-size: 25px;
  text-align: center;
}

.hobbie_border{
  border :solid gray;
  margin-right: auto;
  margin-left: auto;
  display: block;
  border-radius: 10px;
  width: 15%;
}


.form_pad{
  padding-top: 5%;
  padding-bottom: 1%;
}

.form_titre{
  font-size: 2em;
  text-align: center;
}

.form_bord{
  border: gray solid;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  border-radius: 10px;
  text-align: center;
  margin-bottom: 20px;
  box-shadow: 2px 2px 5px black;
}

.input_padding input{
  width:90%;
  border: black solid 2px;
  border-radius: 5px;
  font-size: 1.2em;
}

textarea{
  width:90%;
  border: black solid 2px;
  resize: none;
  border-radius: 5px;
  font-size: 1.2em;
}

.input_padding{
  padding: 10px;
}

.input_padding_textarea{
  padding-top: 20px;
}

.input_padding button{
  padding: 10px;
}

.bouton{
  background: linear-gradient(to bottom , white, rgb(136, 134, 134));
}

.bouton:active{
  background: linear-gradient(to top , white, rgb(136, 134, 134));
}

input:focus::placeholder {
  color: transparent;
}

textarea:focus::placeholder {
  color: transparent;
}

@media (max-width:1106px){
  .projet_flex{
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    display: block;
    padding-left: 0%;
    padding-right: 0%;
  }
}

@media (max-width:1145px){
  .infos_grille{
    display: grid;
    grid-template-columns: 100%;
  }


  .infos_mail{
    margin-left:0%;
  }
  
  .infos_tel{
    margin-right:0%;
  }
  
  .infos_linkedin{
    margin-left:0%;
  }
  
  .infos_cv{
    margin-right:0%;
  }
}

@media (max-width:532px){
  .infos_text{
    font-size: 1em;
  }
}

@media (max-width:1157px) {
  .skill_border2{
    width:20%;
  }
}

@media (max-width:873px){
  .skill_border2{
    width:30%
  }
}

@media (max-width:594px){
  .skill_border2{
    width: 45%;
  }
}

@media (max-width:1168px){
  .hobbie_border{
    width: 30%;
  }
}

